.file-item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 364px;
  padding: 18px 23px;
  font-size: rem(13px);
  line-height: rem(17px);
  color: $dark-text;
  text-decoration: none;
  user-select: none;
  border: 1px solid $light-grey;
  border-radius: $border-radius;
  outline: none;
  transition: color $style-change-duration;

  &:focus {
    color: $blue-accent;
  }

  &:focus:not(.focus-visible) {
    color: $blue-default;
  }

  &:hover:not(.focus-visible) {
    color: $blue-accent;
  }
}

.file-item svg {
  flex-shrink: 0;
  display: block;
  width: 24px;
  height: 24px;
  margin-right: 12px;
}

.file-item__info {
  flex-grow: 1;
  margin: 0;
  overflow: hidden;
}

.file-item__name {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.file-item__size {
  text-transform: uppercase;
  color: $grey-default;
}
